.container-achievements {
  width: 100%;
  max-width: 1440px;
  margin: auto;
  padding: 0 100px;
}

.gallery {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 50px;
}

@media screen and (max-width: 1000px) {
  .container-achievements {
    padding: 0 20px;
  }

  .gallery {
    gap: 20px;
  }
}
