.container-contact {
  width: 100%;
  max-width: 1440px;
  margin: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0 100px;
  gap: 50px;
}

.container-contact section {
  width: 100%;
}

.container-contact .block-content-container {
  width: 100%;
  background-color: #323232;
  color: #fff;
  display: flex;
  flex-direction: column;
}

.contact .block-content {
  width: 80%;
  margin: auto;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.contact h2 {
  margin-bottom: 50px;
  font-size: 36px;
  font-weight: 600;
  border-bottom: solid 3px #9179ef;
  height: 55px;
}

.contact p {
  font-size: 18px;
  font-weight: 400;
}

.contact a {
  text-decoration: none;
  color: #a088fc;
}

@media screen and (max-width: 1000px) {
  .container-contact {
    gap: 20px;
    padding: 0 20px;
  }
  .contact p {
    font-size: 16px;
  }
  .contact h2 {
    font-size: 24px;
    margin-bottom: 20px;
  }
}

@media screen and (max-width: 600px) {
  .contact h2 {
    font-size: 18px;
  }

  .contact p {
    font-size: 12px;
  }
}
